import tabbar from '../../common/tabbar.vue';
import vipInfo from '../../common/vipinfo/vipinfo.vue';
import http from '../../common/http.js';
let weichat =  'wangquietforyou';
export default {
	data() {
		return {
			contentHeight: 0,
			platform: 0,
			isIphoneX: false,
			marginTop: '',
		}
	},
	components:{
		tabBar:tabbar,
		vipInfo,
	},
	onShow() {
		if(this.platform == 2) {
			let globalData = getApp().globalData;
			if(globalData.isRecharge == 1) {
				console.log('loginRequest....');
				this.loginRequest(true);
			}
		}
	},
	onShareAppMessage() {
		let pages = getCurrentPages(); //获取加载的页面
		let currentPage = pages[pages.length-1]; //获取当前页面的对象
		let url = currentPage.route; //当前页面url
		return {
			title: '个人中心',
			path: url,
			success: function() {
			},
			fail: function() {
			}
		};
	},
	onLoad(option) {
		//this.login();
		let winHeight      = uni.getSystemInfoSync().windowHeight;
		console.log('winHeight', winHeight);
		if (this.isIphoneX) {
			winHeight =  winHeight - 34;
		}
		let globalData = getApp().globalData;
		this.isIphoneX = globalData.isIphoneX;
		this.platform = globalData.platform;
		console.log('platform', this.platform);
		// 设计稿731 高度
		this.contentHeight = winHeight-60 -60;
		console.log(winHeight, this.contentHeight);
		console.log('onLoad....');
		// 判断缓存里面，是否有用户信息,有就自动登录
		let userInfo = this.getUserInfoFromStorage();
		if(userInfo.openid) {
			this.login(false);
		}
	},
	created() {
		uni.showShareMenu({
		    withShareTicket: true
		});
		console.log('created....');
	},
	computed: {
		userInfo: function() {
			return this.$store.getters.userInfo;
		},
		level: function() {
			if (this.$store.getters.userInfo.level) {
				return this.$store.getters.userInfo.level;
			}
			return 0;
		},
		dates: function() {
			let userInfo = this.$store.getters.userInfo;
			let dateStr = '';
			if (userInfo.start_time) {
				dateStr+=userInfo.start_time+"~";
			}
			if (userInfo.end_time) {
				dateStr+=userInfo.end_time;
			}
			console.log('dateStr', dateStr);
			return dateStr;
		}
	},
	methods: {
		// 关于我们
		about_us_article() {
			uni.navigateTo({
				url:'/pages/user/about_us'
			});
		},
		// 填写个人信息
		personal_info() {
			uni.navigateTo({
				url:'/pages/user/complaint'
			});
		},
		handleComplaint() {
			//this.getUserInfoFromWeixin();
			uni.navigateTo({
				url:'/pages/user/complaint'
			});
		},
		upgrade_vip() {
			uni.navigateTo({
				url:'/pages/user/upgrade_user_vip'
			});
		},
		copy_customer_wechat() {
			uni.setClipboardData({
				data: weichat,
				success: function(res) {
					uni.getClipboardData({
						success: function(res) {
							uni.showToast({
								title: '复制成功'
							});
						}
					});
				}
			});
		},
		setUserInfo(userInfo) {
			this.$store.commit('setUserInfo', userInfo);
		},
		login(hideLoading) {
			if (!hideLoading) {
				uni.showLoading({
					title: '登录中...'
				});
			}
		   // 1.wx获取登录用户code
			this.loginRequest(hideLoading);
		},
		loginRequest(hideLoading) {
			let _this = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					let code = loginRes.code;
					//console.log(loginRes);
					if(!hideLoading) {
						uni.hideLoading();
					}
					//非第一次授权获取用户信息
					let data = getApp().globalData;
					const apiPrefix = data.serverUri;
					const auth = data.auth;
					const url = apiPrefix + "?mod=user&ac=wx_user";
					//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					uni.getUserInfo({
						provider:'weixin',
						success: (res) => {
							//console.log('res:', res);
							let userInfo = res.userInfo;
							//console.log(userInfo);
							uni.request({
								url:url,
								method: 'POST',
								header: {
									'Content-Type': 'application/x-www-form-urlencoded'
								},
								data:{
									code: code,
									auth: auth,
									nickname: userInfo.nickName,
									avatarurl: userInfo.avatarUrl,
									city: userInfo.city,
									country: userInfo.country,
									gender: userInfo.gender,
									province: userInfo.province
								},
								success: (resp) => { 
									//openId、或SessionKdy存储//隐藏loading
									//console.log(resp);
									resp = resp.data.data;
									let uid = resp.id;
									userInfo.uid = uid;
									userInfo.level = resp.cid;
									if(resp.start_time) {
										userInfo.start_time = resp.start_time;
									}
									if(resp.end_time) {
										userInfo.end_time = resp.end_time;
									}
									if(resp.openid) {
										userInfo.openid = resp.openid;
									}
									//_this.dates = userInfo.start_time + "~" + userInfo.end_time;
									//console.log('userInfo:', userInfo);
									_this.setUserInfo(userInfo);
									_this.setUserInfoToStrorage(userInfo);
									if(hideLoading) {
										data.isRecharge = 0;
									}
								},
							});
						},
						fail: () => {
							console.log("未授权");
						}
					});
				}
			});
		},
		setUserInfoToStrorage(userInfo) {
			const userStr = JSON.stringify(userInfo);
			//console.log('userStr', userStr);
			uni.setStorage({
				key: 'wx_userinfo',
				data: userStr,
				success: function () {
					console.log('wxSetUserInfo...success');
				},
				fail:function() {
					console.log('wxSetUserInfo...fail');
				}
			});
		},
		getUserInfoFromStorage() {
			const userStr = uni.getStorageSync('wx_userinfo');
			//console.log('userStr from storage:', userStr);
			if(userStr) {
				return JSON.parse(userStr);
			} else {
				return '';
			}
		},
		// 手动授权方法,授权登录的时候,只调用一次
		wxGetUserInfo(e) {
			//console.log('get_user_info');
			this.login();
		},
	}
}

